<template>
    <index>
        <template slot="main">
            <div class="title">
                <el-popover placement="right-end" title="审核条件" width="350" trigger="hover">
                    <el-radio-group v-model="radio" size="mini" @change="radioChange">
                        <el-radio :label="1">不选择</el-radio>
                        <el-radio :label="2">已通过审核</el-radio>
                        <el-radio :label="3">审核中</el-radio>
                    </el-radio-group>
                <el-button slot="reference">筛选</el-button></el-popover>
                <div style="padding:0 0 15px 0;width:300px;float:right"><el-input placeholder="请输入名字或学号" v-model="search_input"><el-button type="primary" slot="append" icon="el-icon-search" @click="search()"></el-button></el-input></div>
            </div>
            <div style="padding:0 0 20px 0"><el-tag :key="tag" v-for="tag in tags" size="mini" :closable="true" @close='closeTag(tag)' :disable-transitions="false">{{tag}}</el-tag></div>
            <div>
                <el-table
                v-loading="loading"
                :data="tableData"
                :default-sort = "{prop: 'clbum', order: 'ascending'}"
                style="width: 100%"
                height="600"
                max-height="600px">
                <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="学号">
                        <span>{{ props.row.stu_id }}</span>
                    </el-form-item>
                    <el-form-item label="姓名">
                        <span>{{ props.row.regis_name}}</span>
                    </el-form-item>
                    <el-form-item label="班级">
                        <span>{{ props.row.clbum }}</span>
                    </el-form-item>
                    <el-form-item label="系">
                        <span>{{ props.row.department }}</span>
                    </el-form-item>
                    <el-form-item label="专业">
                        <span>{{ props.row.major }}</span>
                    </el-form-item>
                    <el-form-item label="联系方式">
                        <span>{{ props.row.mobile }}</span>
                    </el-form-item>
                    <el-form-item label="常驻国家">
                        <span>{{ props.row.resident }}</span>
                    </el-form-item>
                    <el-form-item label="常驻地址1">
                        <span>{{ props.row.addr1 }}</span>
                    </el-form-item>
                    <el-form-item label="常驻地址2">
                        <span>{{ props.row.addr2 }}</span>
                    </el-form-item>
                    <el-form-item label="Email">
                        <span>{{ props.row.e_mail }}</span>
                    </el-form-item>
                    <el-form-item label="常用社交账号">
                        <span>{{ props.row.social_account }}</span>
                    </el-form-item>
                    </el-form>
                </template>
                </el-table-column>
                <el-table-column prop="stu_id" label="学号" sortable width="180"></el-table-column>
                <el-table-column prop="regis_name" label="姓名" sortable width="180"></el-table-column>
                <el-table-column prop="clbum" label="班级" sortable width="180"></el-table-column>
                <el-table-column prop="major" label="专业" sortable width="180"></el-table-column>
                <el-table-column prop="portrait" label="头像" width="180">
                    <template slot-scope="scope">
                        <img :src="scope.row.portrait" style="width:50px">
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="审核状态" width="180">
                    <template slot-scope="scope" width="100">
                        <el-select v-model="scope.row.status" placeholder="请选择审核状态" >
                            <el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">提交</el-button>
                    </template>
                </el-table-column>
            </el-table>
            </div>
            <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 50]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </template>
    </index>
</template>

<script>
import index from './index'
export default {
    name:'audit',
    components:{
        'index':index
    },
    data(){
        return{
            tableData:[],
            total:0,
            pageSize:10,
            currentPage: 1,
            status:[
                {
                    value : "0",
                    label : '审核未通过'
                },
                {
                    value : "1",
                    label : '审核通过'
                },
                {
                    value : "2",
                    label : '审核中'
                },
            ],
            loading:true,
            radio:1,
            tags:[],
            tags_label:1,
            search_input:''
        }
    },
    methods: {
        formatter(row, column) {
            return row.address;
        },
        handleClick(row) {
            var _this = this
            let params = {
                stu_id:row.stu_id,
                status:row.status
            }
            _this.$server.reunion_auditUpdate(params).then((res) =>{
                if(res.code == 200){
                    _this.$message({
                        showClose:true,
                        message:'提交成功',
                        type:'success'
                    })
                }else{
                    _this.$message({
                        showClose:true,
                        message:'提交失败',
                        type:'error'
                    })
                }
            }).catch((err) =>{
                console.log(err)
            })
        },
        handleSizeChange(vals) {
            var _this = this
            _this.loading = true
            let params = {
                tags_label:_this.tags_label,
                pageSize:vals,
                page:_this.currentPage
            }
            _this.$server.alumni_auditScreen(params).then((res) =>{
                if(res.length > 0){
                    _this.tableData = res;
                    _this.loading = false;
                    _this.total = res.length
                }
            }).catch((err) =>{
                console.log(err)
            })
        },
        handleCurrentChange(val) {
            var _this = this
            _this.loading = true
            let params = {
                tags_label:_this.tags_label,
                pageSize:_this.currentPage,
                page:val
            }
            _this.$server.alumni_auditScreen(params).then((res) =>{
                console.log(res)
                if(res.length > 0){
                    _this.tableData = res;
                    _this.loading = false;
                    _this.total = res.length
                }
            }).catch((err) =>{
                console.log(err)
            })
        }, 
        radioChange(e){
            var _this = this
            _this.loading = true
            _this.tags_label = e
            if(e == 1){
                _this.tags = []
                _this.tags_label = e
            }else if(e == 2){
                _this.tags = ['已通过审核']
            }else{
                _this.tags = ['审核中']
            }
            let params = {
                tags_label:e,
                page:_this.currentPage,
                pageSize:_this.pageSize
            }
            _this.$server.alumni_auditScreen(params).then((res) =>{
                _this.tableData = res
                _this.total = res.length
                _this.loading = false
            })
        },
        closeTag(tag){
            this.tags.splice(this.tags.indexOf(tag), 1);
            var _this = this
            let params = {
                tags_label:_this.tags_label,
                pageSize:_this.pageSize,
                page:_this.currentPage
            }
            _this.$server.alumni_audit(params).then((res) =>{
                if(res.length > 0){
                    _this.tableData = res;
                    _this.loading = false;
                    _this.total = res.length
                }
            }).catch((err) =>{
                console.log(err)
            })
        },
         search(){
             var _this = this
             if(_this.search_input == ""){
                 _this.$message({
                    showClose:true,
                    message:'搜索框内不能为空！',
                    type:'warning'
                })
             }else{
                 _this.loading = true
                 let params = {
                     tags_label:_this.tags_label,
                     value:_this.search_input,
                 }
                 _this.$server.alumni_auditSearch(params).then((res) =>{
                     _this.tableData = res;
                     _this.total = res.length;
                     _this.loading = false
                 })
             }
         }
    },
    mounted(){
        var _this = this
        let params = {
             tags_label:_this.tags_label,
            pageSize:_this.pageSize,
            page:_this.currentPage
        }
        _this.$server.reunion_audit(params).then((res) =>{
            console.log(res)
            if(res.length > 0){
                _this.tableData = res;
                _this.loading = false;
                _this.total = res.length
            }
        }).catch((err) =>{
            console.log(err)
        })
    },
}
</script>

<style scoped>
.title{
    height: 50px;
}
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
</style>



