<template>
    <div>
        <div class="title"><router-link class="title-button" to="/home/create_mp"><el-button type="primary">新建小程序</el-button></router-link></div>
        <div v-for="(item,key) in mp_data" :key="key">
            <div>
                <el-row >
                    <el-col :span="8" v-for="(item1,key1) in mp_data[key]" :key="key1">
                        <el-card shadow="hover" :header="item1.app_id">
                            <div slot="header" class="clearfix">
                                <span>{{item1.mp_name}}</span>
                                <el-button style="float: right; padding: 3px 0 0 20px" type="text" @click="enter(item1)">进入</el-button>
                                <el-button style="float: right; padding: 3px 0 0 10px" type="text" @click="edit(item1.mp_id)">编辑</el-button>
                            </div>
                            <div class="text item">
                                创建日期：{{item1.date}}
                            </div>
                        </el-card>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
                <!-- <div class ="title">
                <el-button class="title-button" type="primary" plain @click="create_mp()">新建小程序</el-button>
                <router-link class="title-button" to="/home/create_mp">新建小程序</router-link>
            </div> -->
</template>

<script>
import Display from './common/Display'
import axios from 'axios'
import baseUrl from '../../static/js/config'
export default {
    name:'Home',
    components:{
        "display":Display
    },
    data(){
        return{
            mp_data:[]
        }
    },
    methods:{
        create_mp(){
            var _this = this
            this.$router.push({
                path:'/home/create_mp'
            })
        },
        enter(e){
            var _this = this
            _this.$router.push({
                path:'/home/alumni?mp_id='+e
            })
        },
        edit(e){
            var _this = this
            _this.$router.push({
                path:'/home/edit_mp?mp_id='+e
            })
        }
    },
    mounted(){
        var _this = this
        this.$server.mp_view()
        .then((res) =>{
            this.mp_data = res
        })
    }
}
</script>
<style scoped>
    .title{
        height: 40px;
        width: 100%;
    }
    .title-button{
        position: absolute;
        top: 80px;
        right: 29px;
    }
    .operation{
        height: 50%;
        width: 50%
    }
    .display{
        height: 30%;
        width: 50%;
    }
      .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>



