<template>
    <div>
        <display>
            <template slot="main">
                <el-form ref="form" :model="form_data" label-width="100px" :rules="rules" >
                    <div>
                        <el-row>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="编号" prop="mp_id">
                                        <el-input v-model="form_data.mp_id" :disabled="true"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="名称" prop="mp_name">
                                        <el-input v-model="form_data.mp_name"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>       
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="App_ID" prop="app_id">
                                        <el-input v-model="form_data.app_id"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="App_Secret" prop="app_secret">
                                        <el-input v-model="form_data.app_secret"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>       
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="日期" prop="date" required>
                                    <el-date-picker type="date" placeholder="选择日期" v-model="form_data.date" style="width:100%"></el-date-picker>
                                    </el-form-item>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="备注" prop="note">
                                        <el-input v-model="form_data.note"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>       
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="小程序账号" prop="e_mail">
                                        <el-input v-model="form_data.e_mail"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div>
                                    <el-form-item label="小程序密码" prop="pwd">
                                        <el-input v-model="form_data.pwd"></el-input>
                                    </el-form-item>
                                </div>
                            </el-col>       
                        </el-row>
                    </div>
                    <div class="form_button">
                        <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form')">提交</el-button></span>
                        <span style="padding:0 0 0 80px"><el-button  @click="resetForm('ruleForm')">取消</el-button></span>
                    </div>       
                </el-form>
            </template>
        </display>
    </div>
</template>

<script>
import Display from './common/Display'
import axios from 'axios'
import baseUrl from '../../static/js/config'
// import { valid } from 'semver';
export default {
    name:'create_mp',
    components:{
        'display':Display
    },
    data(){
        return{
            form_data:{},
            rules:{
                mp_name:[
                    {required:true,message:'请输入小程序名称',trigger:'blur'}
                ],
                date:[
                    {type:'date',required:true,message:'请选择日期',trigger:'change'}
                ],
                app_id:[
                    {required:true,message:'请输入小程序App_ID',trigger:'blur'}
                ],
                app_secret:[
                    {required:true,message:'请输入小程序App_Secret',trigger:'blur'}
                ],
                e_mail:[
                    {required:true,message:'请输入小程序账号',trigger:'blur'}
                ],
                pwd:[
                    {required:true,message:'请输入小程序密码',trigger:'blur'}
                ],
                note:[
                    {required:true,message:'请输入备注（用途）',trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        submitForm(formName){
            var _this = this
            _this.$refs[formName].validate((valid) => {
                if(valid){
                    axios.post(baseUrl.basePath + '/base/mp_single',{
                        value:_this.form
                    }).then((res) =>{
                        console.log(res)
                    }).then((err) =>{
                        console.log(err)
                    })
                }else{
                    return false
                }
            })
        },
        resetForm(formName){
            var _this = this
            _this.$refs[formName].resetFieds();
        }
    },
    mounted(){
        var _this = this

    }
}
</script>

<style scoped>
    .el-form-item{
       padding: 0 50px
    }
    .el-input{
        width: 100%
    }
    .el-row{
        padding: 30px 0 0 0
    }
    .el-col{
        padding: 0 0 0 50px
    }
    .form_button{
        text-align: center
    }
</style>



