<template>
    <display>
        <template slot='main'>
            <div>
                <el-table
                    :data="tableData"
                    height="400"
                    style="width: 100%">
                    <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="小程序 ID">
                            <span>{{ props.row.mp_id }}</span>
                        </el-form-item>
                        <el-form-item label="小程序名称">
                            <span>{{ props.row.mp_name }}</span>
                        </el-form-item>
                        <el-form-item label="小程序描述">
                            <span>{{ props.row.note }}</span>
                        </el-form-item>
                        <el-form-item label="App_ID">
                            <span>{{ props.row.app_id }}</span>
                        </el-form-item>
                        <el-form-item label="App_Secret">
                            <span>{{ props.row.app_secret }}</span>
                        </el-form-item>
                        <el-form-item label="登陆账号">
                            <span>{{ props.row.e_mail }}</span>
                        </el-form-item>
                        <el-form-item label="登陆密码">
                            <span>{{ props.row.pwd }}</span>
                        </el-form-item>
                        </el-form>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="小程序 ID"
                    prop="mp_id">
                    </el-table-column>
                    <el-table-column
                    label="小程序名称"
                    prop="mp_name">
                    </el-table-column>
                    <el-table-column
                    label="描述"
                    prop="note">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="150">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="enter(scope.row)" type="text" size="small">进入</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
            </div>
        </template>
    </display>
</template>

<script>
import Display from './common/Display'
import axios from 'axios'
import baseUrl from '../../static/js/config'
export default {
    name:'Alumni',
    components:{
        'display':Display
    },
    data(){
        return{
            tableData:[],
            pageSize:10,
            currentPage: 1,
            total: new Number()
        }
    },
    methods:{
        handleSizeChange(val){
            var _this = this
            let pages = {
                page:_this.currentPage,
                pageSize:val
            }
            _this.$server.mp_list(pages).then((res) =>{
                _this.tableData = res
            }).catch((err) =>{
                console.log(err)
            })
        },
        handleCurrentChange(val){
            var _this = this
            let pages = {
                page:val,
                pageSize:_this.pageSize
            }
            _this.$server.mp_list(pages).then((res) =>{
                _this.tableData = res
            }).catch((err) =>{
                console.log(err)
            })

        },
        handleClick(row){
            var _this = this
            _this.$router.push({
                path:'/home/edit_mp?mp_id='+row.mp_id
            })
        },
        enter(row){
            var _this = this
            _this.$router.push({
                path:'/home/'+'mp_'+row.mp_id
            })
        }
    },
    mounted(){// 获取小程序alumni 用户信息
        var _this = this
        let pages = {
            page:_this.currentPage,
            pageSize:_this.pageSize
        }
        _this.$server.mp_list(pages).then((res) =>{
            _this.tableData = res
            _this.total = res.length
        }).catch((err) =>{
            console.log(err)
        })
    }
}
</script>

<style scoped>
    .mp_title{
        text-align: center;
        font-size: 20px;
    }
      .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



