import Vue from 'vue'
import Router from 'vue-router'
import Mp_view from '@/components/Mp_view'
import Login from '@/components/Login'
import Create_mp from '@/components/Create_mp'
import Home from '@/components/Home'
import Alumni_index from '@/components/reunion/index'
import Mp_index from '@/components/Mp_index'
import Edit_mp from '@/components/Edit_mp'
import Reunion_meeting from '@/components/reunion/meeting'
import Reunion_audit from '@/components/reunion/audit'
import Reunion_complain from '@/components/reunion/complain'
import Reunion_affairs from '@/components/reunion/affairs'

Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',redirect:'/login'},
    {path:'/login',name:'login',component:Login},
    {path:'/home',name:'home',component:Home,meta:{requireAuth:true}},
    {path:'/home/mp_view',name:'mp_view',component:Mp_index},
    {path:'/home/create_mp',name:'create_mp',component:Create_mp},
    {path:'/home/edit_mp',name:'edit_mp',component:Edit_mp},
    {path:'/home/mp_1',redirect:'/home/reunion_meeting',name:'reunion'},

    /** 
     * 上海会议签到模块
    */
    {path:'/home/reunion_meeting',name:'meeting',component:Reunion_meeting},
    {path:'/home/reunion_audit',name:'audit',component:Reunion_audit},
    {path:'/home/reunion_complain',name:'complain',component:Reunion_complain},
    {path:'/home/reunion_affairs',name:'affairs',component:Reunion_affairs}
  ]
})



