<template>
    <el-container>
        <el-header>
            <div><img style="width:200px;padding:13px 0 0 0;float:left" src="../../assets/new_logo.png" alt=""></div>
        </el-header>
        <el-container>
            <el-aside width="200px">
                <el-menu
                id="nav"
                :default-active="this.$router.history.current.path"
                class="el-menu-vertical-demo"
                router 
                @open="handleOpen"
                @close="handleClose"
                @select="handleSelect"
                active-text-color="#409eff">
                <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
                    <i class="el-icon-menu"></i>
                    {{item.navItem}}
                </el-menu-item>
            </el-menu>
            </el-aside>
            <el-main>
                <slot name="main"></slot>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import Display from '../common/Mp_display'
import axios from 'axios'
import baseUrl from '../../../static/js/config'
export default {
    name:'Alumni',
    components:{
        'display':Display
    },
    data(){
        return{
            mp_data:[],
            table_data:[],
             navList:[
                    {name:'/home/alumni_meeting',navItem:'会议管理'},
                    {name:'/home/alumni_audit',navItem:'信息审核'},
                ],
        }
    },
    methods:{
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        handleSelect(key,keyPath){

        }
    },
    mounted(){// 获取小程序alumni 用户信息
        var _this = this
    }
}
</script>


<style scoped>
    .el-header {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
    .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    /* line-height: 200px; */
  }
  .el-main {
    background-color: #E9EEF3;
    /* color: #333; */
    /* text-align: center; */
    /* line-height: 160px; */
  }
  
</style>



