<template>
    <index>
        <template slot="main">
            <div class="title">
                <el-popover placement="right-end" title="申诉条件" width="450" trigger="hover">
                    <el-radio-group v-model="radio" size="mini" @change="radioChange">
                        <el-radio :label="1">不选择</el-radio>
                        <el-radio :label="2">已通过申诉</el-radio>
                        <el-radio :label="3">申诉中</el-radio>
                        <el-radio :label="4">申诉未通过</el-radio>
                    </el-radio-group>
                <el-button slot="reference">筛选</el-button></el-popover>
                <div style="padding:0 0 15px 0;width:300px;float:right"><el-input placeholder="请输入名字或学号" v-model="search_input"><el-button type="primary" slot="append" icon="el-icon-search" @click="search()"></el-button></el-input></div>
            </div>
            <div style="padding:0 0 20px 0"><el-tag :key="tag" v-for="tag in tags" size="mini" :closable="true" @close='closeTag(tag)' :disable-transitions="false">{{tag}}</el-tag></div>
            <div>
                <el-table
                    :data="tableData"
                    style="width: 100%"
                    v-loading='loading'
                    height="600"
                    max-height="600px"
                    :default-sort = "{prop: 'date', order: 'descending'}"
                    >
                    <el-table-column prop="plaintiff" label="姓名" sortable width="220"></el-table-column>
                     <el-table-column prop="portrait" label="头像" width="180">
                        <template slot-scope="scope">
                            <img :src="scope.row.portrait" style="width:50px">
                        </template>
                    </el-table-column>
                    <el-table-column prop="plain_phone" label="联系方式" width="200"></el-table-column>
                    <el-table-column prop="plain_time" label="申诉时间" sortable width="200"></el-table-column>
                    <el-table-column prop="plain_status"  label="申诉状态" width="200">
                         <template slot-scope="scope" width="100">
                            <el-select v-model="scope.row.plain_status" placeholder="请选择审核状态" >
                                <el-option v-for="item in plain_status" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">提交</el-button>
                    </template>
                </el-table-column>
                </el-table>
            </div>
            <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[20, 30, 40, 50]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </template>
    </index>
</template>

<script>
import index from './index'
export default {
    name:'Complain',
    components:{
        'index':index
    },
    data(){
        return{
            tableData:[],
            currentPage:1,
            pageSize:20,
            total:0,
            loading:false,
            radio:1,
            tags:[],
            tags_label:1,
            search_input:'',
            plain_status:[
                {
                    value : 0,
                    label : '申诉未通过'
                },
                {
                    value : 1,
                    label : '申诉通过'
                },
                {
                    value : 2,
                    label : '申诉中'
                },
            ],
        }
    },
    methods:{
        formatter(row, column) {
            return row.address;
        },
        handleClick(row) {
            console.log(row)
            var _this = this
            let params = {
                id:row.complain_id,
                openid:row.open_id,
                plain_status:row.plain_status,
                name:row.plaintiff,
                plain_phone:row.plain_phone,
                portrait:row.portrait
            }
            _this.$server.reunion_renewalComplain(params).then((res) =>{
                if(res.code == 200){
                    _this.$message({
                        showClose:true,
                        message:'提交成功',
                        type:'success'
                    })
                }else if(res.msg == '再次操作'){
                    _this.$message({
                        showClose:true,
                        message:'已通过，请不要再次进行操作',
                        type:'warning'
                    })
                }else{
                    _this.$message({
                        showClose:true,
                        message:'操作失败',
                        type:'error'
                    })
                }
            }).catch((err) =>{
                console.log(err)
            })
        },
        handleSizeChange(){},
        handleCurrentChange(){},
        radioChange(e){
            var _this = this
            _this.loading = true
            _this.tags_label = e
            if(e == 1){//不选择
                _this.tags = []
                _this.tags_label = e
            }else if(e == 2){//通过申诉
                _this.tags = ['通过申诉']
            }else{//申诉中
                _this.tags = ['申诉中']
            }
            let params = {
                tags_label:e,
                page:_this.currentPage,
                pageSize:_this.pageSize
            }
            _this.$server.reunion_captureComplain(params).then((res) =>{
                _this.tableData = res
                _this.total = res.length
                _this.loading = false
            }).catch((err) =>{
                console.log(err)
            })
        },
        closeTag(tag){
            var _this = this
            _this.tags.splice(this.tags.indexOf(tag), 1);
            _this.loading = true
            _this.radio = 1
            let params = {
                tags_label:1,
                pageSize:_this.pageSize,
                page:_this.currentPage
            }
            _this.$server.reunion_captureComplain(params).then((res) =>{
                _this.tableData = res
                _this.total = res.length
                _this.loading = false
            }).catch((err) =>{
                console.log(err)
            })
        },
    },
    mounted() {
        var _this = this
        _this.loading = true
        let params = {
            tags_label:_this.tags_label,
            pageSize:_this.pageSize,
            page:_this.currentPage
        }
        _this.$server.reunion_captureComplain(params).then((res) =>{
            console.log(res)
            _this.tableData = res
            _this.total = res.length
            _this.loading = false
        }).catch((err) =>{
            console.log(err)
        })
    },
}
</script>

<style scoped>
.title{
    height: 50px;
}
</style>



