<template>
    <div>
        <index>
           
            <template slot="main">
                <div style="background:white;height:600px">
                    <el-form label-position='right' label-width="80px" :model="form_data" class="demo-ruleForm" status-icon :rules="rules" ref="form_data" >
                        <div>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="名称" prop="name" >
                                        <el-input v-model="form_data.name" autocomplete="off"></el-input>
                                    </el-form-item>
                                </div></el-col>
                                <el-col :span="12"><div>
                                    <el-form-item label="地点" prop="place">
                                        <el-input v-model="form_data.place" autocomplete="off"></el-input>
                                    </el-form-item>
                                </div></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="开始时间" prop="start_time">
                                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form_data.start_time" style="width:100%"></el-date-picker>
                                    </el-form-item>
                                </div></el-col>
                                <el-col :span="12"><div>
                                    <el-form-item label="截至时间" prop="start_time">
                                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form_data.end_time" style="width:100%"></el-date-picker>
                                    </el-form-item>
                                </div></el-col>
                                
                            </el-row>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="会议阶段" prop="meeting_status" >
                                        <el-select v-model="form_data.meeting_status" placeholder="请选择阶段" >
                                            <el-option label="会议前" value="1"></el-option>
                                            <el-option label="会议中" value="2"></el-option>
                                            <el-option label="会议后" value="3"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div></el-col>
                                <el-col :span="12"><div>
                                    <el-form-item label="会议附注" prop="note" >
                                        <el-input type="textarea" v-model="form_data.note" autocomplete="off"></el-input>
                                    </el-form-item>
                                </div></el-col>
                            </el-row>
                        </div>
                        <div class="form_button">
                                <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form_data')">提交</el-button></span>
                                <span style="padding:0 0 0 80px"><el-button  @click="cancel()">取消</el-button></span>
                        </div>
                    </el-form>
                </div>
            </template>
        </index>
    </div>
</template>

<script>
import index from './index'
import display from '../common/Mp_display'
export default {
    name:'meeting',
    components:{
        'index':index,
        'display':display
    },
    data(){
        var check_name = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('请输入年会名称'))
            }
        }
        var check_start_time = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('请确认会议开始时间'))
            }
        }
        var check_end_time = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('请确认会议截至时间'))
            }
        }
        var check_place = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('会议举行地点不能为空'))
            }
        }
        var check_note = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('会议提醒不能为空'))
            }
        }
        return{
            form_data:{
                name:'',
                start_time:'',
                end_time:'',
                place:'',
                meeting_status:'',
                note:'',
            },
            rules:{
                start_time:[
                    {validator:check_start_time,trigger:'change'}
                ],
                end_time:[
                    {validator:check_start_time,trigger:'change'}
                ],
                place:[
                    {validator:check_place,trigger:'blur'}
                ],
                note:[
                    {validator:check_note,trigger:'blur'}
                ],
            }
        }
    },
    methods:{
        submitForm(formData){
            var _this = this
            let params = {
                data:_this.form_data
            }
            _this.$server.reunion_renewalMeeting(params).then((res) =>{
                if(res.code == 200){
                    _this.$message({
                        showClose:true,
                        message:'提交成功！',
                        type:'success'
                    })
                }else{
                    _this.$message({
                        showClose:true,
                        message:'提交失败！',
                        type:'error'
                    })
                }
            }).catch((err) =>{

            })
        }
    },
    mounted(){
        var _this = this
        _this.$server.reunion_captureMeeting().then((res) =>{
            if(res.length > 0){
                _this.form_data = res[0]
            }
        }).catch((err) =>{
            console.log(err)
        })
    }
}
</script>

<style scoped>
.el-form-item{
    padding: 0 50px
}
.el-input{
    width: 100%
}
.el-row{
    padding: 30px 0 0 0
}
.form_button{
    text-align: center
}
</style>



