import { get, post, put } from './http-service'

export const server = {
    mp_view:function(res){
        return get('/base/mp_list',res)
    },
    login:function(res){
        return post('/base/login',res)
    },
    mp_list:function(res){
        return get('/base/mp_list',res)
    },
    edit_mp:function(res){
        return put('/base/edit_mp',res)
    },
    single_mp:function(res){
        return get('/base/mp_list',res)
    },
    reunion_captureMeeting:function(res){
        return get('/reunion_cli/meeting',res)
    },
    reunion_renewalMeeting:function(res){
        return post('/reunion_cli/meeting',res)
    },
    reunion_captureComplain:function(res){
        return get('/reunion_cli/complain',res)
    },
    reunion_renewalComplain:function(res){
        return post('/reunion_cli/complain',res)
    },
    reunion_audit:function(res){
        return get('/reunion_cli/audit',res)
    },
    reunion_auditUpdate:function(res){
        return post('reunion_cli/audit',res)
    },
}


